import { Component, ViewEncapsulation } from '@angular/core';
import { SignupService } from '../services/SignUpService';
import 'rxjs/add/operator/map';
import { Http, Headers, RequestOptions } from '@angular/http';
import { UsersModel } from '../models/UsersModel';

declare var jQuery: any
@Component({
    selector: 'agency-advanced-admin-signup',
    styles: [require('./agency-advanced-admin-signup.style.scss')],
    template: require('./agency-advanced-admin-signup.template.html'),
    encapsulation: ViewEncapsulation.None,
    providers: [SignupService],
    host: {
        class: 'login-page app'
    }
})
export class AgencyAdvancedAdminSignup {
    AgencyAdvancedAdminName: string;
    AgencyAdvancedAdminUsername: string;
    counter: Number;
    createdByUserId: string;
    userToBeCreatedRole: string;
    userObject:UsersModel;

    constructor(private signupService: SignupService, private http: Http) {
        this.userObject = JSON.parse(sessionStorage.getItem('userObject'));
        jQuery(document).ready(function ($) {


            $('.signUp').attr('disabled', true);


            $('#AgencyAdvancedAdmin .field').keyup(function () {

                var empty = false;
                $('.field').each(function () {
                    if (($(this).val().length == 0) || this.value.length > 30 || this.value.length < 8) {
                        empty = true;
                    }
                });

                if (empty) {
                    $('.signUp').attr('disabled', true);
                } else {

                    var emailcheck = $("#AgencyAdvancedAdminUsername").val();
                    var re = /^(([^<>()\[\]\.,;:\s@\"]+(\.[^<>()\[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i;
                    if (emailcheck.match(/^(([^<>()\[\]\.,;:\s@\"]+(\.[^<>()\[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i)) {
                        $('.signUp').attr('disabled', false);
                    } else {
                        $('.signUp').attr('disabled', true);
                    }

                }
            });

        });

    }



    SignUpNewAgencyAdvancedAdmin() {
        var counter = 1;
        this.userToBeCreatedRole = "5";
        
        this.createdByUserId = sessionStorage.getItem('userId');
        if (counter > 0) {
            jQuery('p.add-another').text("Add another one?");
            jQuery(".field").val("");
        }
        console.log(this.AgencyAdvancedAdminName, this.AgencyAdvancedAdminUsername, this.userToBeCreatedRole, this.createdByUserId);
        this.signupService.addUser(this.AgencyAdvancedAdminName, this.AgencyAdvancedAdminUsername,
            this.userToBeCreatedRole, this.createdByUserId, this.userObject.agencyName,  sessionStorage.getItem('token')).subscribe(

            a => {
                if (a.code == 200) {
                    
                    jQuery("#successBox").text("Invitation Sent Successfully");
                    $(".field").val('');
                    jQuery("#successBox").css("display", "block");
                    jQuery("#errorBox").css("display", "none");
                    //this.router.navigate(['/app/dashboard']);
                    jQuery('#btns').show();
                    jQuery('#loader').hide();
                } else if (a.code == 400) {
                    jQuery("#successBox").css("display", "none");
                    jQuery("#errorBox").text("The proces failed");
                    jQuery("#errorBox").css("display", "block");
                } else if (300) {
                    jQuery("#successBox").css("display", "none");
                    jQuery("#errorBox").text("A user with this email already exists");
                    jQuery("#errorBox").css("display", "block");
                } else if (a.code == 500) {
                    jQuery("#successBox").css("display", "none");
                    jQuery("#errorBox").text("Your are an unauthorized user");
                    jQuery("#errorBox").css("display", "block");
                }
                else {
                    jQuery('#btns').show();
                    jQuery('#loader').hide();
                    jQuery('#wrongLogin').show();
                    console.log(a.message);
                }
            }


            );
    }



}



